import Mock from 'mockjs';
import { param2Obj } from 'utils';


const List = [
  {
    id: Mock.mock('@increment'),
    title: 'EOS架构和腾讯架构的区别',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_1.png',
    type: 'EOS',
    aspect: '技术',
    remark: '有人说理想的区块链架构应该是这样的,但是EOS的架构却是这样的,对比下微信的架构后，得出结...',
    content: `<p>有人说理想的区块链架构应该是这样的</p>
      <p><img style="width: 100%;" src="http://7b1fck.com1.z0.glb.clouddn.com/2018-06-14-WechatIMG127.jpeg" alt="WechatIMG127"></p>
      <p>但是EOS的架构却是这样的</p>
      <p><img style="width: 100%;" src="http://7b1fck.com1.z0.glb.clouddn.com/2018-06-14-WechatIMG125.jpeg" alt="WechatIMG125"></p>
      <p>对比下微信的架构后，得出结论：EOS和腾讯一样是中心化的。</p>
      <p><img style="width: 100%;" src="http://7b1fck.com1.z0.glb.clouddn.com/2018-06-14-WechatIMG126.jpeg" alt="WechatIMG126"></pp>
      <p>首先想说的是，这个结论，或者说类比，是不对的。</p>
      <p>最大的问题是他把21个节点看成是21台电脑。但实际上，21个EOS超级节点是一台电脑。</p>
      <p>如果非要类比，那么21个节点之间的通讯，应该类比成腾讯一台服务器上的总线通讯。</p>
      <p>因为21个EOS超级节点是一台电脑，所以，每一个EOS用户都可以看到这台电脑的数据，运行这台电脑的程序。</p>
      <p>而腾讯的服务器，你能够看到的数据，只是他允许你看到的，你能够操作的，只是他允许你操作的。</p>
      <p>这个就是区块链应用和互联网应用本质的不同。</p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'EOS：区块链未来的王者，还是2018年最大的陷阱？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_2.png',
    type: 'EOS',
    aspect: '技术',
    remark: '近来，关于谁是未来区块链王者的争夺战越来越激烈。EOS、IOST、TRON……纷纷向V神的以太...',
    content: `<p>近来，关于谁是未来区块链王者的争夺战越来越激烈。EOS、IOST、TRON……纷纷向V神的以太坊发起挑战。&nbsp;</p>
    <p>这其中，EOS无疑是“最耀眼”的明星。</p><p>双方主将EOS之BM与ETH之V神的隔空对决；各自阵营背后社区势力对比；还有EOS发起对ETH的价格绞杀……好不热闹。</p>
    <p>那么，EOS是未来区块链3.0的代表，还是2018年最大的陷阱？</p>
    <p>在投资领域，这种案例比比皆是，众多的韭菜被各种逐渐高涨的舆论裹挟着，奋不顾身跳进庄家设置的一个个陷阱……</p>
    <p>乐视网的“贾布斯”给大家画的饼够大吧？不要说韭菜，就连机构和明星，最后连融创中国的董事会主席孙宏斌都统统一起收割。</p>
    <p>比特大师从客观的立场出发，对EOS发出几点质疑，希望对广大投资者有一些启发。</p>
    <p>在最热闹的时候，我们必须守住内心的宁静。</p>
    <p>看到所有关于EOS的项目宣传，有一个共同点，就是调子特别高。选一个有代表性的， 大家一起来看看：</p>
    <p>“区块链1.0是共识技术、加密算法和数字签名结合而产生，解决去中心化的信任问题，代表为比特币系统；区块链2.0是区块链1.0+智能合约，将货币应用推广到商业活动中，代表为以太坊系统；区块链3.0是区块链2.0+低延时高并发等媲美中心化应用的商业表现，推动区块链更快地融入实际生活中，代表为EOS系统。”</p>
    <p>低延时、高并发、媲美中心化……华丽的辞藻背后，把安全性给忽略了。公链难得三全法：安全、扩容、去中心。如果只讲两点，就是耍流氓。</p>
    <p><strong>质疑一：EOS上会出现大规模商业应用吗？</strong></p>
    <p>个人认为，目前区块链还处于2.0的完善阶段，区块链3.0的出现，要以出现大规模商业应用为前提，在出现大规模商业应用场景之前，说自己代表区块链3.0，都是耍牛氓。很显然，EOS现在只是描绘了一个3.0的可能性，应用方面能否达到要求，现在是没谱的事情，而且个人偏向于EOS很难做到，这个等到6月份，也许能见分晓。</p>
    <p>这就有点像乐视网的贾布斯了，画一个巨大无比的饼，结果只有在最后才能揭晓，是变成腾讯还是乐视？这些都是一个未知数。</p>
    <p>而即使6月份主网上线，那也是刚刚开始，距离真正一统江湖还早着呢。</p>
    <p><strong>质疑二：EOS“白皮书”因何避重就轻？</strong></p>
    <p>DPOS（Delegated Proof of Stake/委托股权证明）构建一套信任系统，通过无摩擦实时投票产生一组总数一定的授信方（受托人），这些受托人拥有产出区块并添加到区块链上的权利。每一轮有21个受托方（EOS是21，bitshare 是101）轮流产生区块（顺序随机），并以私钥签署命令。区块中的交易不可更改。如果如果受托方没有按照规定产生区块，将会被随后发生的投票踢出。</p>
    <p>EOS的创造者BM（全名为ByteMaster）还专门为DPOS写了一篇文章，并称之为EOS“缺失的白皮书”，可见DPOS对EOS的重要程度，在这份“缺失的白皮书”里，BM把DPOS算法分成了两部分：</p>
    <p>1.选举区块生产者（如何选举）</p>
    <p>2.调度生产（如何达成共识）</p>
    <p>原文对第一部分一句话带过，对第二部分作了大篇幅的介绍。如何选举涉及到每个参与者的核心利益，是整个体系的基础；而调度生产只是一个共识达成的机制，是如何让第一步实现的技术支撑。因此个人认为，这里第一部分重要性远大于第二部分，BM此举有避重就轻之嫌，如果BM是有意如此，那这里面的问题可能就非常大了。</p>
    <p><strong>质疑三：被财阀统治的EOS？</strong></p>
    <p>当前，EOS全球21个主节点之争拉开序幕。</p><p>何为EOS的21个超级节点？举例来说，就是一个老师，想要提高全校学生的成绩，却懒得教书，于是找了21个人来做题，让其他人来抄。</p><p>EOS的超级节点就是超级矿工，他们的主要工作是记录和计算EOS网络中的全部数据。不同于其他分布式网络的是，EOS的每个超级旷工记账时再也无需将数据向全球所有旷工广播，而只需向另外20个节点广播，如此一来整个分布式网络的运行速度就会呈指数级增长。</p><p>为什么会有这么多「庄家级」玩家入场竞选，是因为这场竞选的 21 位获选者每年都能有「巨额」的收入。</p><p>作为主节点，他们将获得 EOS 每年增发 5% 的收益中的大部分，大约每一个节点每年可以获得238 万个 EOS的收益，按照当前价格（EOS/RMB ￥44.2），一个节点每年可以分到1 亿元人民币的奖励。&nbsp;</p><p>如果 EOS 在主网上线后，真的颠覆了 ETH 甚至整个区块链生态，成为了信徒们口中的「百倍币」，那么这个 21 个超级节点每年将瓜分近2100 亿人民币的巨额财富。让人更加错愕的是，这已经是所谓「保守」预计了，在币圈，相信在 2020 年 EOS 将突破 1000 美金的疯狂支持者大有人在。&nbsp;</p><p>注意，这并不是制造「百亿」富翁，而是制造「每年获得百亿」的富翁。除此之外，这些超级节点还将拥有 EOS 生态的投票权，权利相当于美国制度中凌驾于总统之上的「大法官」，整个 EOS 生态中的任何应用都无法绕开他们的「裁决」。&nbsp;</p><p>这些将造成EOS生态中权利的高度集中，即V神指出的财阀统治，屌丝将失去话语权，为了拉票获得权利，贿选，腐败等社会中存在的问题，将出现在EOS的生态中。</p>
    <p><strong>质疑四：提币跑路还是拉盘？</strong></p>
    <p><img style="width: 100%;" src="https://img.jinse.com/723961_image3.png" alt="EOS：区块链未来的王者，还是2018年最大的陷阱？"></p>
    <p>EOS zc账户提币越来越密集，大额提币越来越多，3月27日提出61.8万ETH，是跑路还是拉盘？</p>
    <p>一：如果是拉盘，有这个必要吗，一个币的长期价值，不是靠操盘决定的，好像EOS团队把精力用错了地方。</p>
    <p><img  style="width: 100%" src="https://img.jinse.com/723963_image3.png" alt="EOS：区块链未来的王者，还是2018年最大的陷阱？"></p>
    <p>什么事都能被EOS的脑残粉解读成大利好，资金盘的介入，最终受伤的都是小散，因为别人来就是收割你的，作为一个运营初期的项目，你把价格拉到虚高，泡沫搞那么大，你想干嘛？</p>
    <p>拉盘对项目的发展是十分不利的，价格越贵，上链的项目成本就会越高，强拉上去的价格泡沫很大，如果人家觉得你泡沫太大，还敢进来吗，这对EOS的应用生态有很大影响。拉盘的好处就是利于套现，利于收割韭菜。</p>
    <p>亏得EOS的脑残粉什么事都能脑补成EOS团队的“良心操作”，如果 zc 钱包提ETH是为了护盘EOS这个舆论是EOS团队引导的，那问题就更严重了（自行脑补）。</p>
    <p>二：如果是跑路，呵呵！！！ 这一切串联起来好像就突然合理了，如果真的发生：将血流成河、尸横遍野，很多人将随这段历史记录在区块链的重大历史事件里，某些疯狂鼓吹EOS的大V将遗臭万年。</p>
    <p><strong>质疑五：屌丝与狗不得入内？</strong></p>
    <p>3月29日，EOS更新了超级节点竞选投票规则：</p>
    <p>任何一个EOS，都可以投出 30 票；1 个 EOS可以投给 30 个节点，或者只投给几个节点，但不能给一个节点投超过 1 票。此举使投票不再具有强制唯一性。</p>
    <p>这个规则很可怕，这样的话像李笑来等手头有大量币的人，一个币可以投三十票，超级节点仅21个，他们就可以包了超级节点。</p>
    <p>最终，贵族、财阀将掌控整个社区，EOS变成了一个超级玩家的游戏，屌丝与狗不得入内，这样的EOS和去中心化已经没有多少关系了。</p>
    <p><strong>总结——躲不开的劫</strong></p><p>EOS的营销确实是业界一流水平，但是他们的资源大部分用在了这些地方；而在技术的研发，应用的落地方面，树绩寥寥。今年作为底层争夺白热化的一年，如果你实际的东西做不出来，就只能被淘汰，被灭亡，希望EOS在技术和应用方面能实现它们所画的大饼。</p>
    <p>再放三张图：</p>
    <p><img style="width: 100%" src="https://img.jinse.com/723969_image3.png" alt="EOS：区块链未来的王者，还是2018年最大的陷阱？"></p>
    <p>EOS团队，搞笑天赋也不错哈哈！一天能修复很多BUG，真牛逼。</p>
    <p><img style="width: 100%" src="https://img.jinse.com/723970_image3.png" alt="EOS：区块链未来的王者，还是2018年最大的陷阱？"></p>
    <p>签名验证都关闭了，真的是在搞区块链吗？哈哈！</p>
    <p>吐槽完毕，欢迎各位大牛指正批评，也欢迎EOS的铁粉来怼。</p>
    <p>比特大师微信：weiju5059</p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'EOS投票钱包指南：你的钱包能投EOS节点吗？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_3.png',
    type: 'EOS',
    aspect: '技术',
    remark: '吸引诸多目光的EOS主网上线，在15%的投票率前陷入焦灼。吸引更多持币者投票，是候选节点...',
    content: `<p><span class="load-html-img loaded"><img alt="EOS投票钱包指南：你的钱包能投EOS节点吗？" data-img-size-val="623,229" style="width: 100%; height: auto;" src="https://pic.36krcnd.com/avatar/201806/14111123/pphzkz468jwy8xs0.png!1200"></span></p><p>编者按：本文来自36氪战略合作区块链媒体“<a href="https://www.odaily.com/p/5132358.html" target="_blank">Odaily星球日报</a>”（公众号ID：o-daily，<a href="https://download.odaily.com/" target="_blank">APP下载</a> <span>）</span></p><p>吸引诸多目光的EOS主网上线，在15%的投票率前陷入焦灼。吸引更多持币者投票，是候选节点们的迫切要求，而投票工具，也成为一片大有空间抢夺流量的蛮荒大陆。</p><p>目前，EOS投票渠道有官方推荐命令行工具cleos、投票网站、交易所以及钱包等四大类投票渠道。但目前来看，面对投票工具操作门槛高、投票网站安全性无三方担保、交易所陷私钥被盗风波的情况，作为普及率较高的币圈工具，钱包因其简易快捷的方式，有望成为投票渠道中十分有潜力的存在。</p><p>在钱包中，EOS投票功能通常为其中一个板块，与资讯行情、交易功能并行，用户同样需将已有EOS默认抵押成投票权，使用自己的私钥为EOS超级节点投票，一个&nbsp;EOS&nbsp;代表一票，一票最多可同时投&nbsp;30&nbsp;个候选节点，每个候选节点最多获得&nbsp;1&nbsp;票。</p><p>目前，支持EOS投票的钱包中，既有此前专做数字货币钱包的专业户，也有不少节点开发的钱包。</p><h3>一、专业钱包</h3><p>作为钱包领域的第一梯队玩家，上线EOS投票功能，不论是对于专业钱包户自身还是对于EOS生态来说都是双赢的结果，一方面利于钱包自身拓展业务，抢占EOS钱包赛道，提高用户渗透率；另一方面也能发挥钱包已有的用户基数，加快投票进度。近期已上线投票功能的钱包有：imtoken、比特派、tokenpocket。</p><p><strong>1、imToken</strong></p><p>6月12日，imToken&nbsp;宣布正式上线&nbsp;EOS&nbsp;钱包和&nbsp;EOS&nbsp;超级节点投票。用户可导入已映射&nbsp;EOS&nbsp;钱包私钥来生成&nbsp;EOS&nbsp;账户，参与超级节点投票。imToken是当前国内最大的以太坊轻钱包，月活已达400万，此次上线EOS投票功能，无疑也会为为进程缓慢的EOS主网投票加一把薪。</p><p><strong>2、比特派钱包</strong></p><p>6月14日，比特派钱包上线最新版本，开放EOS投票功能。比特派钱包是由比太团队研发的比特币钱包，也是比特币官方唯一认可的数字资产钱包，用户基数也较为庞大。</p><p><strong>3、TokenPocket</strong></p><p>数字货币钱包TokenPocket声称是一款支持多币种、多底层、跨链交易的通用数字钱包，4月初支持EOS主网映射，6月初上线EOS钱包与EOS超级节点投票功能。</p><h3>二、节点队</h3><p>根据eosportal.io显示，目前EOS投票参与率为10%，比起前三日速度略有回升，但在此前距离主网上线所需的15%投票率还有一段距离之际，不论是面对缓慢增长的投票率还是出于抢占EOS钱包赛道的需求，诸多超级节点都按耐不住，加快了上线自家钱包的脚步。</p><p><strong>1、Pomelo</strong></p><p>Pomelo是MEET.ONE、EOS&nbsp;Cannon、EOSIO.SG、EOS&nbsp;Nation、EOSeoul等节点联手开发的钱包，代码开源，于6月初上线，该钱包允许用户导入EOS私钥，完成交易、对超级节点进行投票，以及检查EOS账户的余额、CPU值、净利润值等。</p><p><strong>2、Tokenika</strong></p><p>Tokenika是来自波兰的候选节点，已推出了EOS&nbsp;factory、TEOS等多个基于EOS的工具，日前也推出了 “Secure&nbsp;EOS&nbsp;Block&nbsp;Producer&nbsp;Voting”，其“让EOS冷钱包实现离线投票”。</p><p><strong>3、Greymass</strong></p><p>同为节点候选项目Greymass开发出一款开源的功能限制版本的轻钱包eos-voter，为EOS区块链设计，该应用程序能够连接到远程EOS&nbsp;API端点，以执行节点投票操作和一些基本的钱包命令。</p><p><strong>4、EOS&nbsp;Local&nbsp;Voter</strong></p><p>EOS&nbsp;Local&nbsp;Voter&nbsp;则是由&nbsp;超级节点候选项目EOSLaoMao&nbsp;团队开发的，开源的&nbsp;EOS&nbsp;投票客户端，声称：旨在为映射账户提供一个更安全的本地投票工具，降低私钥泄漏的风险。</p><p><strong>5、HaloWallet</strong></p><p>HaloWallet是超级节点候选人EOS引力区孵化的一款EOS钱包，为解决用户EOS映射操作困难的问题而诞生，内置钱包资产管理、积分兑换EOS、钱包支付等功能，目前在其最新版本中也已上线EOS“一键投票”功能。</p><p>主网上线投票进程中，预计还会有更多钱包上线投票功能，但总的来说，钱包投票与其他任何投票方式一样，同样都需要用户私钥，原因在于：1、验证投票是否合法；2、将该投票与用户的持有量相关联，后者用于确定投票的权重。钱包投票方式仍然存在不安全的地方，用户在投票时也一定注意交易所私钥被盗教训，手动输入私钥投票。</p><p><span class="load-html-img loaded"><img  style="width: 100%" alt="EOS投票钱包指南：你的钱包能投EOS节点吗？" data-img-size-val="1200,464" src="https://pic.36krcnd.com/avatar/201805/18052544/434zuwfnuv2upkdz.jpg!1200"></span></p><p>（我是Odaily星球日报记者芦荟，爆料、交流请加微信1012387983，烦请备注姓名、单位、职务和事由。）</p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'imToken EOS钱包映射教程',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_4.png',
    type: 'EOS',
    aspect: '技术',
    remark: '目前大家众筹和使用的 EOS 代币其实并不是 EOS 主网代币，都是基于以太坊 ERC-20 的 EO...',
    content: `<p><img style="width: 100%" src="./static/img/article/article_1.jpg" alt="eos"/></p><p style="text-align: justify;"><strong><span style="font-size: 16px;">一：什么是 EOS 映射，我们为什么要在众筹结束前做 EOS 映射呢？</span></strong></p><p style="text-align: justify;"><span style="font-size: 15px;">目前大家众筹和使用的 EOS 代币其实并不是 EOS 主网代币，都是基于以太坊 ERC-20 的 EOS 代币。并且 EOS 官方白皮书明确说明不承认 ICO 期间 EOS 代币的任何价值，所以我们需要将以太坊上的 EOS 代币兑换成 EOS 主网代币。</span></p><p style="text-align: justify;"><span style="font-size: 15px;">EOS 众筹将在北京时间 2018 年 6 月 2 日06:59:59结束，众筹结束后 23 小时内，以太坊上的 EOS 代币将停止转账。所以在众筹结束之前，用户必须生成一个 EOS 主网钱包，以便在 EOS 主网启动之后，获得相同数量的 EOS 主网代币，这个过程称为 EOS 钱包映射。</span></p><p style="text-align: justify;"><span style="font-size: 16px;"></span></p><hr><p style="text-align: justify;"><strong><span style="font-size: 16px;">二：关于映射的注意事项，这里要强调几点：</span></strong></p><p style="text-align: justify;"><span style="font-size: 15px;">1. 钱包中 EOS 数量大于 0 才可以进行映射</span></p><p style="text-align: justify;"><span style="font-size: 15px;">2. imToken 目前不支持冷钱包和观察钱包进行映射</span></p><p style="text-align: justify;"><span style="font-size: 15px;">3. 映射需要花费少量 ETH 作为矿工费</span></p><p style="text-align: justify;"><span style="font-size: 15px;">4. imToken 映射过程需要二次校验 EOS 的私钥，而不是 imToken 钱包的私钥</span></p><p style="text-align: justify;"><span style="font-size: 15px;">5. EOS 私钥同 imToken 钱包私钥同样重要，需要妥善保管</span></p><p style="text-align: justify;"><span style="font-size: 15px;">6. 可以多次映射，以最后一次的映射为准（保管好最后一次生成的私钥）</span></p><p style="text-align: justify;"><span style="font-size: 15px;">7. 映射针对的是地址，向完成映射的地址再次转入 EOS 是不需要再次映射的</span></p><p style="text-align: justify;"><span style="font-size: 15px;">8. 若 imToken 管理的多个地址都有 EOS 你需要为每一个地址进行映射操作</span></p><p style="text-align: justify;"><span style="font-size: 15px;">9. 根据上文的内容，映射的截止时间是北京时间 2018 年 6 月 2 日 06:59:59&nbsp;</span></p><p style="text-align: justify;"><span style="font-size: 15px;">以上几点注意事项就是最近用户常见的问题。从这些问题来看，还是大家对映射这个原理掌握的不够扎实。映射可以简单的理解为 imToken 钱包地址与 EOS 主网地址建立一一对应联系的一个过程，这个过程中你获得了 EOS 主网的私钥和公钥。等到 EOS 主网上线，你能够通过私钥管理 EOS 主网代币。</span></p><hr><p><strong><span style="font-size: 16px;">三：EOS钱包映射过程</span></strong></p><p style="text-align: justify;"><span style="font-size: 15px;">1.打开imToken 1.4.2并点击“Discover”下的“Candy Bar”进入EOS映射页面。</span></p><p style="text-align: center;"><img style="width: 100%" src="./static/img/article/article_2.jpg" alt="eos"/></p><p style="text-align: center;"><br></p><p style="text-align: justify;"><span style="font-size: 15px;">2.如果您尚未映射EOS钱包，请单击“开始”以生成EOS钱包公钥和私钥，务必安全地备份EOS钱包私钥。建议将它复制在纸上。由于EOS钱包的私钥由51个字符组成，因此备份错误的可能性更高，imToken决定通过将私钥分为四个字符组来协助用户。电子备份仅建议使用专业密码管理工具，如1Password。切勿将截图保存在网络上！</span></p><p style="text-align: center;"><img style="width: 100%" src="./static/img/article/article_3.jpg" alt="eos"/></p><p style="text-align: center;"><br></p><p style="text-align: justify;"><span style="text-align: justify;font-size: 15px;">3.在输入框中输入您已备份的私钥以执行第二次验证（注意：私钥字符之间没有空格，将其作为字符串输入）。检查“我确认私钥已正确备份”，然后点击“已阅读预防措施和映射”按钮。最后，您将发送0 ETH交易以呼叫EOS合约。发送交易需要矿工费。等待矿工确认交易并完成EOS钱包映射。</span><br></p><p style="text-align: center;"><img style="width: 100%" src="./static/img/article/article_4.jpg" alt="eos"/></p><p style="text-align: center;"><br></p><p style="text-align: justify;"><strong><span style="font-size: 15px;">注意：</span></strong></p><p style="text-align: justify;"><span style="font-size: 15px;">1.该过程将EOS公钥映射到EOS智能合约。未来，EOS主网络启动后将发布EOS主网络令牌。因此，如果您丢失或泄露了您的EOS钱包私钥，您将无法获得本地EOS主网络令牌。</span></p><p style="text-align: justify;"><span style="font-size: 15px;">2.建议使用专业密码管理工具备份私钥或将其写在纸上，并确保安全保存。</span></p><p style="text-align: justify;"><span style="font-size: 15px;">3.不建议使用第三方工具来映射EOS钱包，因为导入私钥的过程会使用户面临高度安全风险。</span></p><p><span style="font-size: 15px;">原文链接：</span><span style="font-size: 15px;text-decoration: underline;"><a href="https://mp.weixin.qq.com/s?__biz=MzIwMzQ0MTUxMQ==&amp;mid=2247484280&amp;idx=1&amp;sn=a195d6592724574547c8a257cc9d76b9&amp;scene=21#wechat_redirect" target="_blank" style="font-size: 15px;text-decoration: underline;">imToken EOS 钱包映射教程</a></span></p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'EOS主网即将上线：或是一场与以太坊在智能合约领域的王权之争',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_5.png',
    type: 'EOS',
    aspect: '观点',
    remark: '英为财情Investing.com-就在这几天，按市值计的第五大加密货币正在加速进入一个备受期...',
    content: `<h2 style="font-size: 16px;"><strong>EOS</strong><strong>迎来新时代：</strong><strong>EOS</strong><strong>自有</strong><strong>主网即将上线</strong></h2>
            <p>英为财情Investing.com-就在这几天，按市值计的第五大加密货币<span class="aqPopupWrapper js-hover-me-wrapper"><a href="/crypto/eos/eos-usd" id="5b2a32c7ac532" class="aqlink js-hover-me" hoverme="aql" data-pairid="1024821">EOS</a></span>正在加速进入一个备受期待的新时代。具体而言，目前作为ERC-20通证运行在<span class="aqPopupWrapper js-hover-me-wrapper"><a href="/crypto/ethereum/eth-usd" id="5b2a32c7ac6c2" class="aqlink js-hover-me" hoverme="aql" data-pairid="997650">以太坊</a></span>区块链上的EOS将迁移至EOS自有主网上运行。</p>
            <p>EOS主网定于6月2日（星期六）上线，许多人相信这将大幅拉升EOS的价格，受此预期影响，EOS已大涨了近500%。然而，于4月29日爆出若干的问题可能拖累EOS迁移至其自有主网的消息后，EOS重挫。</p>
            <p><div class="analysisMedia"><img src="https://d6-invdn-com.akamaized.net/content/picf73ef8790b7634c989b16a7ce5b4494d.png" alt="EOSUSD Weekly" title="EOSUSD Weekly" width="100%"><span class="caption">EOSUSD Weekly</span></div></p>
            <p>事实上，网络上一直存在许多有关EOS新平台存在安全瑕疵和其它缺陷的传闻。但新平台的幕后开发者在Twitter上否认了有关传闻：</p>
            <blockquote>
            <p><em> “EOSIO V1</em><em>会因软件缺陷可能推迟发布是媒体的错误报道。我们团队已经修复了大多数问题，并在努力解决其余的问题。</em><em>EOSIO V1</em><em>将按计划推出；请关注</em><em>EOSIO</em><em>频道获取官方信息</em><em>”</em></p>
            </blockquote>
            <p>尽管开发者做出了回应并修复了一些问题，但似乎投资者并不急于购入EOS，而是持观望态度。</p>
            <p>EOS由开曼群岛公司Block.one创建，于2017年年中推出。该项目由Dan Larimer领导，Dan Larimer还是Steem和Bitshares的创始人。Steem是社交平台Steemit的加密货币，Bitshares是买卖加密货币的去中心化平台。EOS的目标是比以太坊更快和更具扩展性。有些人认为终有一天EOS将超越按市值计的第二大加密货币以太坊。</p>
            <p>近期有关EOS主网修复效率和推出时间的不确定性是否打压了EOS的未来价格？</p>
            <p><a href="https://hosho.io/" target="_blank" rel="noopener">Hosho</a>首席执行官和联合创始人Yo Kwon表示，虽然开发者作出了相当大的努力保证迁移工作正常进行，但这仍然是非常重大的改变。EOS不仅仅已经成为按市值计的最大加密货币之一，而且每名所有者都可能需要注册或使用新钱包以确保他们能够在主网推出后认领EOS币。</p>
            <p>Yo Kwon还指出，主网启动时最大的担忧将是安全性。虽然开发者在不断快速修复测试网，但最近仍然发现了许多漏洞。Yo Kwon认为，在发布主网平台和运行智能合约的环境前，EOS需要与许多第三方网络安全公司进行全面检查。当然，为了发现漏洞，需要更多时间。</p>
            <h2 style="font-size: 16px;"><strong>EOS</strong><strong>与以太坊：从依赖到自立门户，再到兵戈相见？</strong></h2>
            <p>随着EOS即将推出自有主网，外界关于EOS迁移工作和这将如何影响EOS价格的看法与以太坊也有着莫大的关系。以太坊最近遭到猛烈抛售，价格重挫，在上<a href="https://hacked.com/ethereum-price-meltdown-and-whats-causing-it/" target="_blank" rel="noopener">周日</a>的某个时段，以太坊在一个小时内下跌了14%。</p>
            <p><div class="analysisMedia"><img src="https://d6-invdn-com.akamaized.net/content/pic52af3908b91981bcb1fb4cdd56e5510e.png" alt="ETHUSD Weekly" title="ETHUSD Weekly" width="100%"><span class="caption">ETHUSD Weekly</span></div></p>
            <p>很多人将以太坊的价格波动和崩塌归咎于EOS抛弃了以太坊平台。</p>
            <p>如我们之前所说，虽然以太坊获得了丰富的资金并受到加密货币领域和现实科技行业大佬的支持，但EOS仍一直被称为以太坊的直接竞争对手。</p>
            <p>能源领域初创公司<a href="http://enosi.io/" target="_blank" rel="noopener">Enosi</a>的首席区块链架构师Samuel Brooks指出，在成熟度和生态系统工具方面以及在开发人员中的知名度方面，以太坊仍然领先于EOS，但仍可能无法阻止EOS通过宣传获得大量关注，至少暂时如此。</p>
            <p>Brooks解释道，在很大程度上，大多数加密货币仍是因其投机价值而被持有，这意味着许多投资者蜂拥买入各种加密货币并非因为这些加密货币的实际价值，并造就了加密货币市场的不幸，使得技术的“时尚性”成为了推动价格走向的因素。</p>
            <blockquote>
            <p><em> </em><em>“虽然据说</em><em>EOS</em><em>比以太坊‘更快’，但</em><em>EOS</em><em>的共识机制在技术上也不太安全。以太坊也在开发提升扩展能力的技术，这将使以太坊在年底前比</em><em>EOS</em><em>运行得‘更快’。在‘信任’和‘性能’之间总有取舍。显然，这两种平台都有很长的路要走，但是除非</em><em>EOS</em><em>更加成熟，否则拥有现实世界应用程序的开发人员在</em><em>EOS</em><em>上发布产品代码时会非常谨慎。”</em></p>
            </blockquote>
            <p>XCHNG首席执行官Charles Manning表示，这是EOS和其生态系统迈出的一大步，但EOS尚未在现实生活的扩展性、安全性、可靠性和接受度方面证明自己。</p>
            <blockquote>
            <p><em> </em><em>“他们距离推出主网只剩不到一周的时间，但仍在进行一些重要的修复工作。不过一旦所有代码都正确执行并得到采用，这个平台将能够与其他可运行智能合约的区块链直接竞争。”</em></p>
            </blockquote>
            <p>这就是人们认为EOS可能导致了以太坊暴跌的原因。但Brooks不同意该观点：“EOS应该不会导致以太坊暴跌，因为EOS还没有解决以太坊已经克服的许多挑战。”</p>
            
            <h2 style="font-size: 16px;"><strong>以太坊仍然是王者</strong></h2>
            <p>许多人一致认为，在相当长的一段时间内，以太坊仍将是智能合约领域的王者。WatermelonBlock联合创始人兼创意总监Elliot Rothfield认为，任何有经验的交易员都会同意：在这个高度投机的市场，传言弥漫时买入，消息确认时卖出。</p>
            <blockquote>
            <p><em> “我们一直在关注有关EOS的全球投资者情绪。虽然现在气氛相当积极，但距竞争对手以太坊570亿美元的市值仍然很遥远。虽然EOS是一个伟大的项目，但在主网推出时，我会谨防出现抛售。不要在见顶时入场。”</em></p>
            </blockquote>                <p class="writtenByWrapper">
            <span class="writer_img">
                             <img style="width: 100%" src="https://i-invdn-com.akamaized.net/companies_logos/121899_1508334774.png" alt="Investing.com">
                         </span>
            <span class="byLine">
                <span class="writtenByPreTitle">作者:</span>
                Investing.com            </span>
            </p><div class="clear"></div>
            <p></p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: '观点：2018必将是EOS的爆发之年',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_6.png',
    type: 'EOS',
    aspect: '观点',
    remark: 'EOS，2018年最值得期待的潜力币,中文叫柚子，EOS的目标是超越以太坊，当前最热门的币...',
    content: `<p>EOS，<strong>2018年最值得期待的潜力币,中文叫柚子，EOS的目标是超越以太坊</strong>，当前最热门的币种，除了BTC和ETH，非EOS莫属！</p>
          <p>EOS最重要的创始人是Daniel Larimer,<strong>他就是江湖传闻的大名鼎鼎的BM</strong>，这个名字是他网名的缩写（他曾经的网名叫Bytemaste），他不仅是EOS的联合创始人，还是比特股（Bitshares）,斯蒂姆币（Steem）的联合创始人。<strong>BM是目前公认的技术天才，09年进入币圈的他，就敢与比特币创始人中本聪叫板</strong>，他的另外两个项目做的也很成功，但是由于性格比较理想化，也曾一度把比特股带入低谷，经历几年的沉浮，BM比之前成熟了许多，由于前车之鉴，BM在EOS项目里只负责技术开发，市场推广由其它人负责。</p>
          <p><strong>EOS可以说是一个区块链开发平台，如果说以太坊是区块链2.0，那么EOS的目标就是做区块链3.0</strong>，也就是胜超说的公链项目，公链项目一旦应用落地成功，价值非常巨大，就像以太坊一样，是具有颠覆性的。</p>
          <p>区块链三大共识的代表项目，比特币是POW（工作量证明），以太坊是POS（权益证明），而EOS采取的是DPOS（股份授权证明）共识，<strong>能实现每秒几百万级别交易请求，将大大提高EOS链上的交易速度</strong>，解决以太坊的交易速度慢的问题。</p>
          <p>ETH的交易速度每秒几十笔交易,BTC的交易速度更慢，理论上最大值每秒只有七笔交易，EOS的目标是使交易速度达到每秒几百万啊，哪怕只有几千笔也会解决很大的一个痛点。</p>
          <p>对于佛系炒币者来说，<strong>假如让胜超只推荐一个币，那肯定是BTC，两个就是BTC和ETH，三个就是BTC，ETH，EOS</strong>，前两个不用说，比特币和以太币都有一定的落地应用，而EOS主网还未上线，还有一定的未知性。</p>
          <p>严格来说，<strong>现在1000多种虚拟货币中，只有比特币和以太坊有应用价值</strong>，其它的都没有，但是，比特币和以太坊涨的倍数已经太多，而EOS首次众筹价是7元，按目前价格42元涨幅才6倍，一旦应用落地成功，未来有很大的上涨空间。</p>
          <p>不过，我<strong>建议在资产配置中，还是要合理配置比例，比如433配置法则，4成配置btc,eth等主流币，3成配置eos潜力币，手中备用3成资金</strong>，用于在熊市中进行分批建仓，当然胜超这里说的433配置的资金，只是用你不影响生活的资金来投资，切记！</p>
          <p>很多新人看到比特币和以太仿价格太高了，不敢配置，而另辟蹊径寻找那些便宜的币，实际上大多数是空气币和传销币，这样是非常危险的，<strong>一旦熊市到来的时候，也只有比特币和以太坊是最保值和最抗跌的，投资的第一原则就是保证本金安全，非常重要。</strong></p>
          <p>就像这次熊市，有很多币都是腰斩腰斩再腰斩，甚至有一些空气币都归0了，等于你投的钱石沉大海，一毛不剩，所以<strong>一定要进行合理的资产配置，不</strong><strong>可以梭哈。</strong></p>
          <p>现在大家一致看好EOS，其实我也看好EOS，但是我认为，应用在没有落地之前，都有一定的风险，不过EOS的风险相对较小，<strong>只要你能够承担风险，就可以去建仓eos，毕竟这样暴增财富的机会一辈子也遇不到几次。</strong></p>
          <p>EOS在6月份主网上线前可以逢低分批建仓，就是越跌越买，不跌就不买，到主网上线后，应该会有一波爆发行情，<strong>但</strong><strong>是我还是建议长期持有EOS，也许未来会是第二个以太坊</strong>，持有三四年，就能获得N倍收益（可以参考eth1800倍），很划算！</p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'EOS深度分析——真的能超越以太坊吗？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_7.png',
    type: 'EOS',
    aspect: '解读',
    remark: '在上一篇《EOS的最大挑战者——ADA深度分析》中，我深度分析了ADA，说到拥抱监管和注...',
    content: `<p>在上一篇《EOS的最大挑战者——ADA深度分析》中，我深度分析了ADA，说到拥抱监管和注重用户隐私是ADA的一大优势。</p><p>现在我分析一下EOS，和ADA同样是底层公链，两个项目的技术负责人之前还是同事，一起合作开发过项目。那么，EOS的优势在哪呢，为什么会这么火呢？EOS真的能超过ETH，成为下一代区块链底层基础设施吗？</p><p>EOS基本介绍</p><p>EOS是区块链公司Block.one开发的区块链底层架构软件，全称是EOS.IO，在这个软件上可以开发DAPP，EOS是这个软件上的代币，利用这些代币可以使用EOS网络中的资源，比如计算资源、存储资源。</p><p>如果你拥有了一些EOS代币，那么，等到EOS主网上线以后，开发者可能会大量租用或购买EOS代币来支撑DAPP的运行，这就是EOS不断升值的主要动力。</p><p>因为巧妙的共识机制和底层技术，让这个EOS项目有了区块链3.0的称号，能不能担当得起这个称号，请看下面的分析。</p><p>EOS创新的地方</p><p><strong>1、DPOS共识算法</strong></p><p>EOS的DPOS共识算法是BM基于权益证明算法（POS）设计的可委托的权益证明算法，相当于咱们的人民代表大会制度，选举出可信任的代表参加会议。而最近火热的超级节点就是EOS共识算法中非常重要的一部分。</p><p>这21个超级节点需要满足一定的硬件、软件、网络条件，才能为EOS网络工作，主要是记账。</p><p><strong>每一次在EOS网络中的交易都需要21个超级节点轮流记账，既然是记账就会有奖励。</strong></p><p>按照目前EOS官方给出的宪法规则，按照总量10亿个eos算，每年会超发5%的EOS，21个节点平均分1%，那就是50万个eos，按照目前50块钱一个eos的价格算，每年收入2000万人民币。</p><p>2000万足以维护一个超级节点了，而且当选超级节点之后的影响力也是各个竞选团队趋之若鹜的地方。</p><p>而普通EOS持有者也可以投票选出自己信任的节点，这样就是全民参与的大好局面，又进一步提高了EOS的知名度。</p><p>至于为什么是21个节点，那是经过数学算法验证过的，不是拍脑袋随便想出来的。</p><p><strong>2、提高开发者速度</strong></p><p>EOS软件提供完善的账号系统，可以帮助开发者快速开发自己的DAPP，而如果要在以太坊上开发DAPP，则需要花更多的时间去开发底层模块。</p><p>在EOS软件上开发就像是在微软的操作系统上开发软件，而在以太坊上开发就像在DOS系统上开发。</p><p>当然，也有一些公链帮助开发者搭建底层模块，比如Rchain，一个美国西雅图的团队做的公链。但相对来说，并没有EOS这么完善。</p><p><strong>像账号系统这样底层的基础模块，EOS软件还开发了很多，这样在EOS上开发DAPP的创业者就不用再重复造轮子了，直接把EOS软件的底层模块拿来用就可以了，把更多的注意力放在业务上。</strong></p><p>以后的底层公链会越来越多的考虑平台的易用性，如果没有这一点，只能当做古董被人束之高阁，或者遗忘。</p><p></p><figure><div class="image-block"><img src="https://ask.qcloudimg.com/http-save/developer-news/z9pi4ug3dt.jpeg" style="width: 100%"></div></figure><p><strong>3、免费</strong></p><p>大家都知道以太坊网络转账是需要消耗“燃料”的，也就是gas，就像开车需要消耗汽油一样，而EOS网络转账是免费的。</p><p>那么，问题来了，比特币、以太坊、瑞波币这些市值较大的区块链网络，要么转账速度很慢，要么转账是需要手续费的，要么，纯粹是个中心化的网络。</p><p>而EOS为什么既可以做到转账速度快、不收手续费，又可以做到多中心呢？</p><p>其实这就是EOS厉害的地方，首先转账快是因为节点少，不需要那么多节点验证。</p><p>其次不收费是因为节点有其他的收入，什么收入呢？EOS设计的规则是每年超发5%，其中4%交给EOS团队保管，剩余1%平均分给超级节点。</p><p>节点有了丰厚的收入，自然就不要向转账交易的用户收取手续费了。</p><p><strong>最后，要做到这两点，节点数目不能多，也不能少，要做到提高效率的同时，提高可靠性，很多人说EOS网络不可靠，其实这样说是不负责任的。</strong></p><p>每个团队都在想办法找到效率与可靠的平衡点，而有幸的是，EOS团队找到了这个最有可能接近平衡点的地方，具体效果如何，让我们拭目以待吧。</p><p><strong>4、关于通胀问题</strong></p><p>EOS每年会增发5%来转嫁用户的交易手续费，其实这样设计是EOS能不能大规模使用的核心。</p><p>为什么这么说，EOS每年超发的5%自然会让你手中的代币贬值，但是这样做也可以带来更大的收益。</p><p>互联网发展至今，已经慢慢出现了很多收费模式，比如微信支付、视频网站、音乐软件，但是大量的软件还是免费的，为什么会这样，就是因为目前大众还是不能接受收费模式。</p><p>要让EOS上的DAPP被大量的普通用户使用，是需要提供免费的服务的，你不能让一个还不了解区块链的普通用户在使用你的EOS网络时还要支付手续费。</p><p><strong>从用户体验的角度上讲，这是不合理的，也是违反人性的。</strong></p><p><strong>所以，EOS网络的这种设计就有了这个优势，也会吸引更多的开发者在EOS网络搭建自己的DAPP。</strong></p><p>一旦，EOS网络上的某一些DAPP被百万级别的用户使用，DAPP开发者团队就需要租用或者购买大量的EOS代币来支撑DAPP的运行和数据存储。</p><p><strong>而EOS代币的需求增多，价格自然就会上去。</strong></p><p>这就是EOS巧妙的地方之一，也是EOS真正要做的事情——让普通用户可以使用区块链网络上的应用，就像使用微信一样。</p><p>扎实的团队</p><p>看一个团队扎不扎实，很重要的一个方面就是代码更新的快不快，写代码真的很辛苦，如果一个团队能够在浮躁的区块链世界，持续的更新代码，而且速度越来越快，这真的是值得称赞的事情。</p><p></p><figure><div class="image-block"><img src="https://ask.qcloudimg.com/http-save/developer-news/ix3xii3gki.jpeg" style="width: 100%"></div></figure><p>这是EOS代码更新的情况，绿色的部分显示，从去年4月份到现在，代码更新速度是逐渐提高的。</p><p>绿色下面黄颜色的部分，你可以看到EOS的技术负责人bytermaster（大家叫他BM）也在不断地贡献代码。</p><p><strong>另外一个方面是团队有没有为了项目顺利落地而想方设法的改进，大家都知道最近区块链比较火热的话题是EOS的超级节点之争，不知不觉已经有了50个左右的团队参加竞选。</strong></p><p>而带些土豪色彩的“温州帮”也宣布要参加竞选，不禁让人想到，EOS已经深入到祖国大地了。</p><p>现在还是预选，等到真正要开始竞选的时候，可能会出现更多潜藏已久的团队宣布要参加竞选，但是只有21个团队被选上，竞争激烈可想而知。</p><p><strong>最近EOS官方为了防止一些团队，为了拉票贿赂选民的情况出现，发布公告，把每年超发的5%个EOS分给超级节点，改成每年1%。</strong></p><p>这样分配是减小超级节点的收入，增加超级节点的作恶成本。让那些善意的参加竞选的团队更有可能竞选上。</p><p></p><figure><div class="image-block"><img src="https://ask.qcloudimg.com/http-save/developer-news/yfjeecttz7.jpeg" style="width: 100%"></div></figure><p>EOS黑客松大赛，总奖金150万美元</p><p>而且，EOS的官网、白皮书、开发计划都在不断的更新，meetup、开发者大赛也都在不断的上演。</p><p>整个团队在稳扎稳打的开展自己的计划。</p><p>投资价值</p><p><strong>1、技术过硬</strong></p><p>EOS的技术负责人BM是自带光环的开发者和连续创业者，其自身的技术实力已经在他的前两个成功的项目中得到验证。</p><p>而且EOS的核心部分——DPOS算法也得到也在前两个项目中得到改进和验证。</p><p><strong>2、社区强大</strong></p><p>区块链项目的投资价值很重要的一个方面是社区，社区足够大，说明大家对这个项目比较认可，那么后期项目上线，会有更多的人使用，这会加快项目的不断向前推进，也会增强开发团队的信心。</p><p></p><figure><div class="image-block"><img src="https://ask.qcloudimg.com/http-save/developer-news/8flbwpw7ev.jpeg" style="width: 100%"></div></figure><p>EOS香港meetup</p><p>EOS在中国的知名度特别高，参加竞选50个团队中有20个是中国的，可想而知，以后的EOS必然会成为中国的EOS。</p><p><strong>3、百万级别交易</strong></p><p>EOS最新版的白皮书中写到：</p><p>在受管控的区块链环境中，可扩展至每秒处理百万级交易。</p><p>BM也在电报群里说过今年6月份上线的主网保守估计可以达到1000TPS，最高可达3万TPS，什么是TPS，就是transaction per second，每秒交易量。</p><p>而通过多线程可以达到几十万TPS，如果再增加侧链的话，可以达到百万级别交易。</p><p>投资风险</p><p><strong>1、项目还未上线</strong></p><p>但是项目终归没有上主网，还处于艾西欧阶段，所以，还是有失败的风险。</p><p><strong>2、技术负责人会不会再次出走</strong></p><p>在之前的两个项目中，BM大神因为和社群意见不合，选择出走，另外创业，这次偏中心化的EOS项目依然有可能会产生几个大的利益集团，到时候BM该怎么选择？</p><p><strong>3、能不能达到百万级别</strong></p><p>一两年后，EOS能不能达到百万级别的TPS，上文说到的数据指标毕竟都是理论数据，并没有在真实的环境下实践过。</p><p>总结</p><p>按照现在的开发进度，6月份按时上线，价格高企是有很大的可能，而且最近的超级节点竞选更是让EOS火了一把。</p><p>但是也有很多风险，每年5%的超发，能不能达到百万级别交易仍然是很多投资者心中的顾虑。</p><p>少侠认为EOS整体市场面是好的，即使短期拿着也是不错的选择。</p><p>注意，本文仅是市场分析，不做投资建议，大家还要独立思考，谨慎谨慎再谨慎。</p><p>EOS短期内不会超过ETH，但是如果在一两年内真能达到百万级别的TPS，你认为EOS会超越以太坊吗？</p><p>为了更好的让大家一起学习交流EOS，我专门建立了一个免费的EOS交流社区——人人EOS，大家扫描即可加入。</p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: '「深度解析」EOS你凭什么挑战以太坊？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_8.png',
    type: 'EOS',
    aspect: '解读',
    remark: '相信大家玩区块链，应该没有不知道EOS的。概况地说，EOS项目是为了打造一个底层操作...',
    content: `<p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">相信大家玩区块链，应该没有不知道EOS的。</span>概况地说，EOS项目是为了打造一个底层操作系统，就好像咱们的Mac系统，或者是Windows系统之类的，在这个基础上，才能创建各种应用软件。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">不同之处在于，EOS是基于区块链技术，并且是去中心化的。</span>这个去中心化系统是为了能够支持工业级别的去中心化应用，例如可以在汽车行业建立去中心化的数据分析，在物流行业建立去中心化的追踪系统等等。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 640px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f11.baidu.com/it/u=2464744576,1727140715&amp;fm=173&amp;s=9A087A23553C418A0BFDE0CA0100C0B3&amp;w=640&amp;h=340&amp;img.JPEG&amp;access=215967316" data-index="0"><span class="openText">打开百度App，看更多美图</span></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 639px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f10.baidu.com/it/u=3910689819,1444392831&amp;fm=173&amp;s=28E27A2692A59B5D005DD08B0100E081&amp;w=639&amp;h=416&amp;img.JPEG&amp;access=215967316" data-index="1"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">EOS</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 436px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f10.baidu.com/it/u=3849991170,3577026529&amp;fm=173&amp;s=5A21716C82A9BF7C58F7A0830200F08B&amp;w=436&amp;h=267&amp;img.JPEG&amp;access=215967316" data-index="2"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p">ETH</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">怎么样？</span><span class="bjh-strong">是不是听起来非常炫酷，但是真正吸引人的还不止是这些。</span><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">EOS项目还打算完全去除转账费用。相信现在使用以太坊转账，大家都能感觉到手续费变得越来越高，这也一直被人诟病。EOS如果能搞定这块，那以太坊真的是要慌了。其次，EOS项目方声称EOS系统可以每秒转账百万次，这个又完爆了以太坊。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">口说无凭，任何人都能站出来说我的系统比以太坊好。别急，接下来我们就来深入分析为何EOS会这么牛。不过在这之前，让我们先来探讨下为何我们需要EOS系统。</span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 640px;"><div style="padding-top: 20px"><img style="width: 100%" src="http://f11.baidu.com/it/u=1991108914,2225711634&amp;fm=173&amp;s=7AA5AC44901643CCCE9B649F0300C09A&amp;w=640&amp;h=391&amp;img.JPEG&amp;access=215967316" data-index="3"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">首先，咱们先来回答一个问题。</span><span class="bjh-strong">去中心化应用需要什么条件？</span><span class="bjh-strong">去中心化应用想要成功最少需要什么条件？</span><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">1. 支持百万级用户数。</span>作为应用软件，无论中心化还是去中心化，都需要能够支持很多用户。如果去中心化应用想要被主流用户接受，那么至少要能够给百万级人数使用才行，所以去中心化应用需要有足够大的扩容性。<span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">2. 免费使用。</span>整个平台或者操作系统，要能够开发出能够免费给用户使用的应用软件。就算去中心化应用有再多好处，用户也不能被强迫必须付费使用。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">3. 系统更新简单便捷。</span>整个操作系统平台应该给开发者足够的自由度来根据他们的想法在任何时候去更新去中心化应用。如果应用中有bug，开发人员可以去修补，但是不能影响整个底层操作系统。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">4. 低延时性。</span>去中心化应该需要很平稳地运行，而且要有很低的延时性，不然打开个网页都得等半天的心情，相信都可以理解的。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">5. 同步运行功能。</span>操作系统平台要能让基于它的去中心化应用同步运行，这样可以合理分配计算量，从而节省时间。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">6. 顺序运行功能。</span>不过也不是区块链上所有的功能都应该是同步运行的。比如转账，多个转账就不可能同步运行，需要一个转完再转另一个，这样可以避免双花，比如你拿着比特币同时去花钱买东西，同时付钱，就可能就有一家收不到钱。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">那么，如果我们想创建去中心化应用，现在有什么操作系统平台可以用呢？</span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong"><span class="bjh-br"></span></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">例如比特股和石墨烯有很好的吞吐量，但是呢，他们不适用于智能合约。</span>现在市场上，很明显大家都是在用以太坊。虽然它有非常强大的智能合约，但是<span class="bjh-strong">转账速度和拥堵是很大的问题</span>。而且，转账需要燃料费用也是阻碍其发展的重大因素。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">EOS系统可以认为是两者优势的结合，拥有石墨烯和比特股的高吞吐量，同时也有以太坊的智能合约能力。</span><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">通过以上的分析，相信现在可以回答为什么我们需要EOS了。<span class="bjh-strong">那么这么厉害的项目背后的团队又是如何呢？他们能承担起这个项目吗？</span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">EOS项目的核心团队是基于开曼群岛的“Block.one”。Brendon Blumer是EOS项目的首席执行官（CEO），在2014年就进入区块链行业。在这之前，他在网络游戏公司和房地产行业都有工作经验。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">Dan Larimer是EOS项目的首席技术官（CTO）。他是DPOS算法的创建者，同时也是去中心化自制组织（DAO）的创建者。同时，他也参与了比特股和Steem。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 639px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f10.baidu.com/it/u=2745539202,1567641629&amp;fm=173&amp;s=EF78A1571F405C415CF41DCA0100E0B1&amp;w=639&amp;h=333&amp;img.JPEG&amp;access=215967316" data-index="4"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">那么EOS系统究竟能做什么呢？我们来看看EOS系统有哪些特性？</span><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">1. 扩容性。</span>现在区块链技术面临到的最大问题就是扩容性，相信大家对以太坊拥堵已经深有体会，一只猫都能分分钟搞瘫它。传统的支付手段例如Visa，每秒可以转账1667次，Paypal每秒转账193次。相比之下，比特币每秒可以转账大约3-4次，以太坊每秒也就20次转账。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">那么为什么基于区块链的TPS（每秒转账数）那么低呢？其实这是因为在现有的区块链系统中，任何的转账都需要网络中所有的节点达成共识，所以速度会很慢。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">EOS系统使用的是DPOS（股份授权证明）共识算法机制，在这种算法机制下，EOS可以很轻松地完成每秒百万转账。先卖个关子，等会我们会详细聊聊为啥这个算法这么牛。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">2. 兼容性。</span>以太坊整个系统在DAO攻击之后就变得有些停滞不前了。所有事情都好像停止了，而且由于硬分叉，社区也开始分裂。由于EOS系统使用的是DPOS算法，在这种生态系统下，以太坊这种分叉的情况几乎不会发生。如果有些去中心化应用有瑕疵，那么被选举出来的区块生产者可以冻结这个应用直到它被整个软件系统所接受。这其实也是DPOS系统的简单延伸，不需要每个节点都参与对链上的管理。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">3.可用性。</span>EOS系统可以允许网页工具包嵌入进行交互界面开发，自我描述的接口，自我描述的数据库以及权限声明。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">4. 治理。</span>EOS生态系统中，治理规则是通过制定管辖权，法律的选择以及其他共同接受的规则。这通常是通过具有约束力的宪法来完成。EOS系统中每笔转账都需要包含这种宪法的哈希签名。这在本质上是把用户绑定到这个宪法之上。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">这种宪法和协议可以通过以下的流程来进行更改： 更改方案由在21人中获得17人赞同的区块链生产者提出，而且这个情况需要保持30天。如果宪法更改成功，所有用户需要使用新宪法的哈希值来签署他们的转账。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">区块生产者需要适应这种改变，并更改相应代码，同时应用到相应的区块链上。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">区块生产者提出的代码需要保持21人中17人同意的比例，并连续维持30天。在这之后，会给予所有的节点一整周的时间来适应新的改变。任何不跟随新的协议的节点会自动关闭。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">所以在EOS系统中，如果发生了类似DAO攻击这种情况，EOS系统会被强制快速寻找现有协议的改变和方案。如果是非常紧急的情况下，区块生产者也有权可以加快更新流程。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">5. 同步处理能力。</span>在同步处理时，程序指令被分到多个处理器。这样做地话，程序运行的时间就会大大减少。EOS系统通过横向扩容，异步通信，互操作性完成智能合约的同步运行。横向扩容其实是和纵向扩容进行对比的，纵向扩容是通过增加处理能力来实现。横向扩容则是通过在资源池里增加更多的系统和计算机来实现。异步通信就是说通信没有同步，所有的参与者无需在同一时间进行通信。互操作性就是指电脑系统有交换和使用信息的能力。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">6. 自给自足的系统。</span>每个基于EOS软件的区块链系统每年都会有5%的自然通胀率。这将会被分发给平台上的区块生产者作为他们对转账进行确认的奖励，同时也会分发给最优的三种智能合约。这样做的原因是因为要确保区块链不会依赖于任何单个的基金，组织或者个人，来进行发展和维护。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">7. 去中心化操作系统。</span>EOS的这个特性是最能让别人明白到底什么是EOS。现在以太坊自称是去中心化的超级计算机，EOS则把自己定位为一个去中心化的操作系统，大家可以想象下如果Mac或者Windows系统有数字经济的激励会是什么样的效果？</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">8. 零转账费用。</span>EOS系统基于一个所有者权益模型，用户拥有同时也可以使用和他们权益正相关的资源，而不是需要支付每笔转账。所以本质上来说，如果你有N个EOS代币，那么你就可以使用N×k比转账，从而取消了转账费用。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">如果开发者想要在以太坊上测试他们的应用，费用会很高。研发初期的燃料费用就足以让开发者破产。以太坊和EOS最基本的区别是以太坊租出他们的计算能力给开发者，而EOS是给予他们资源的所有权。所有本质上来看，如果你有一千分之一的EOS权益，你就有EOS系统中所有计算能力和资源的千分之一。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">还记得之前埋的坑吗？到底什么是DPOS（股份授权证明）算法呢？</span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">现在我们就来讲讲共识算法机制。相信大家都知道，现在最通用的共识算法就是PoW（工作量证明）</span>，使用最多的就是在比特币。但是，EOS系统使用的是DPOS（股份授权证明）共识算法。那么这个算法又是如何运作呢？在说之前，我们先来了解POS（权益证明）算法，毕竟DPOS是基于它的。<span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 500px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f12.baidu.com/it/u=2443020722,956126114&amp;fm=173&amp;s=0C41A9444BB196574C614C0B0300F0C0&amp;w=500&amp;h=310&amp;img.JPEG&amp;access=215967316" data-index="5"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">简单地说，权益证明（POS）算法就是让整个挖矿过程虚拟化，用验证者代替矿工。那么DPOS和POS算法有何不同呢？</span>首先，任何拥有和EOS整合的区块链上代币的用户可以通过投票系统来选择区块生产者。任何人可以参与区块生产者的选举，同时他们也可以生产和“他们获得投票数/所有其他生产者获得投票数”比例的区块数。DPOS系统不会有分叉的现象出现，因为区块生产者必须要一起合作而不是一起竞争来寻找区块。并且如果有分叉，那么共识会自动转到最长的那条区块链。<span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">在DPOS区块链上所有的区块生产者都会参与其中，而且一次转账大约会在1.5秒内确认</span>，为了确保转账绝对的可靠性，21个节点中有15个达到共识就可以转账，暂且称之为15/21法则。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">那么如果这时候产生疏忽或者恶意的分叉会怎么样呢？</span>所有节点都不会自动转移到分叉链上，因为分叉链上没有区块生产者可以满足上面所说的15/21法则，而任何区块只有满足这个法则才会成为整个链的一部分。</span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 639px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f10.baidu.com/it/u=3910689819,1444392831&amp;fm=173&amp;s=28E27A2692A59B5D005DD08B0100E081&amp;w=639&amp;h=416&amp;img.JPEG&amp;access=215967316" data-index="6"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">那么EOS系统是不是就是十全十美了呢？</span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">其实并不是，EOS系统的问题在于太过依赖于投票机制。</span>投票制度其实有以下问题，首先有可能最后投票的参与度会很低，影响投票结果。其次也会可能有这种情况，例如用户把币都存在了交易所，交易所有可能会代替他们去投票，但是用户并不是很在意到底交易所会把票投向何处。也就是说有时候代币持有者的兴趣点和用户的是可能不完全一样的。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">而且，在零转账手续费机制中，用户需要持有一定量的EOS代币从而可以获得一定量的EOS免费转账额度</span>，比如N个EOS代币可以获得N×k个免费转账额度, 但是这对经济条件不好的人来说就不太公平，因为他们可能买不起那么多代币，有钱人可以买更多代币，而且还获得更多免费转账的额度。而且对于一些很少用区块链的用户，可能只是用几次而已，这种所谓免费转账的机制对他们来说没有太大意义，而且这类用户还不少。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">试想如下的情形，在蓝色链上出现了红色链，这时候如何才能防止矿工去挖红色链从而产生分叉呢？</span>在POW（工作量证明）算法情况下，这种风险能被减轻，因为哪怕有个矿工自己去挖红色链，但是其他矿工不参与，因为在蓝色链上挖矿收益更高，而且更加安全，他也没办法去做出分叉。所以在POW机制下，去进行分叉会浪费矿工很多的资源，因为挖出来的区块有可能被网络给否决，但是这些资源可以在原先的链上获得很大的收益且无风险。</span></p><div class="contentMedia commonPadding"><div class="contentImg" style="max-width: 640px;"><div style="padding-top: 20px;"><img style="width: 100%" src="http://f12.baidu.com/it/u=3519018092,3533603713&amp;fm=173&amp;s=CB87C30A4078858A5ECFF1C9020050BA&amp;w=640&amp;h=298&amp;img.JPEG&amp;access=215967316" data-index="7"></div></div></div><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">但是在POS（权益证明）中，整个情况又不一样了。</span>因为POS中没有挖矿，只有验证者（validator）, 如果你作为验证者，可以在蓝色链和红色链上都放资金，那么无论你做出多么恶意的分叉行为，你都会稳稳地有收益。这就叫做“账本分叉问题”。<span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">现在EOS系统存在的问题是他们对于这样做的验证者惩罚不够严重</span>，不像以太坊会严重惩罚每一个这样做的验证者，所以EOS的系统有存在“账本分叉问题”的可能性。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">总结</span><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">很明显，EOS想要和以太坊在“去中心化应用平台”这方面竞争。他们都有非常厉害的技术以及非常强大的团队。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p">但是就像我们之前所说，EOS并不是没有任何缺陷的，而且整个系统也需要时间去开发。与此同时，以太坊也有一些非常优质的扩容策略，例如plasma，分片等等。</span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-br"></span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"><span class="bjh-strong">不论怎样，让我们拭目以待，看看EOS究竟能为我们带来什么！</span></span></p><p class="contentText contentFont  commonPadding"><span class="bjh-p"></span></p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: '如何看待EOS在ICO描述的内容比ETH还厉害？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_9.png',
    type: 'EOS',
    aspect: '观点',
    remark: '众筹一年的时间，第一天2亿，后面350天每天200万，总发行量9亿，值得投资吗？我最早回答这...',
    content: `<p><strong><span>众筹一年的时间，第一天2亿，后面350天每天200万，总发行量9亿，值得投资吗？</span></strong></p><p><span></span></p><p><span>我最早回答这个问题时是在EoS众筹期第70个认筹天（EoS一个认筹天为23小时），当时这个回答很多人已经一片哀鸿，因为从初始认筹的3美元高点，已经跌到接近0.5美元。当我开始侃侃而谈时，几乎没人相信。在此之后的2个月，EoS的认筹价一直徘徊在这个位置左右，如果跟进认筹，1个ETH可以换得大概500个EoS，则这个收益应该超过绝大部分参与者。</span></p><p><span>回顾EoS的融资，不仅创下了ICO的多项纪录，在整个公开市场融资历史上，也是一个重大里程碑式的事件。长达350天的认筹周期，20万人次参与ICO，累计筹得720万个以太币，按当天市价折算，总额达到43亿美元，这个融资额度接近谷歌（27亿美元）和Twitter（21亿美元）的IPO总和，是有史以来到目前为止最大的一宗ICO。这很值得进行全面的总结，我很期望所写的内容，能够穿透事实表面，抓到背后所隐藏的本质，成为历史记载的一部分。</span></p><p><span>EoS的ICO是采用以太坊的智能合约完成自动认筹和派筹，无任何中间人，不存在传统证券上市的投行和发行人承销人角色，这是一级市场。EoS币总数10亿，其中9亿派发给认筹者，1亿为Block.one公司保留。认筹所得资金全部归属于Block.one公司，同时按认筹协议，这些资金直接成为该公司利润，并不存在与认筹者的任何其他责任和义务关系，也不存在维护和开发EoS软件及网络的责任和义务，EoS主网将由松散化组织的技术论坛社区启动，网络运行资金由代码规则自动生成发行新的EoS代币支付给区块生产者节点。</span></p><p><span>ICO过程中二级市场（交易所）同步全面开放，一般的融资在一级市场认筹完成后，有一段时间才会进入二级市场流通，而这次融资周期长达350天，认筹所得以太坊EoS令牌，当天就可以进入二级市场流通，产生大量的换手交易，两个市场的价格随之同步波动。</span></p><p><span>EoS在ICO结束后总市值110亿美元，位列数字货币排行榜第5位，较为逼近第4位的BCH（170亿美元），与第3位的Ripple（240亿美元）则差距超过一倍。相较认筹资金总额43亿美元而言，按照ICO的流行说法，其实不过区区一个3倍币。这是很关键的一个情况，透露出几方面的信息。在ICO的历史上，如果按照比特币或以太坊计价，其实是不存在千倍币的，100倍币有4种，2014年认筹时一个比特币可以兑换2000个以太坊，至今大概上涨了150倍。10倍币有8种，1-10倍涨幅的有11种，而90%的ICO数字货币都是破发，即上市后总市值还没有参与认筹的资金多。所谓千倍万倍基本都是炒作，忽略了绝大部分的基本事实，比如说一个币上市后，因为缺乏流动性，花0.1个BTC就可以给它炒高几百倍，然后迅速破发奔着归零而去，那么它算是百倍币还是破发？当然是破发了。最近更是据说有个币发行量500个，只流通一个，价格已经上天。因此这种ICO的涨幅，只应该将全面上市，流动性非常充足的币种纳入计算。为什么大部分的币都会破发，其实道理很简单，类似风险投资里面的天使投资，投100万占10%的股份，表面上看这家公司应该估值是1000万，但是如果投完马上上市，基本上这家公司账上资产就是100万（可能已经花掉一大部分）以及创始人的信用，那么这样千千万万个初创公司在二级市场的交易价格，破发是很正常的，也就是总市值往往会低于100万。这些公司需要等到有明确的盈利模型出现，才能够卖出较好的价钱，里面更多的仍然是破产、关门、归零。ICO将风险投资的门槛下降为接近为零（EoS的ICO参与认筹的起点只需0.1ETH，约50美元），砍掉了传统风险投资市场的VC、PE、IPO等这些中间过程和多次筛选流程，认筹完毕立即上市，大部分项目没有实质内容，只有题材和故事，大面积破发其实反而是正常的。所以，EoS作为一个3倍币，在ICO里面的涨幅表现是属于靠前的。</span></p><p><span>其二，超长的认筹周期，市场充分消化了EoS的所有利好利空，使之成为了最广为人知的项目，几乎所有稍微懂点数字货币的人都可以进去认筹，也可以直接在二级市场上购买，每个人都有机会塞满一口袋，实际上也是塞满了。既然如此，认筹的资金已经是巨额，市场的预期全部堆进去了，涨起来自然不是那么容易。以太坊的ICO认筹资金只有1800万美元，凡是能够涨百倍的，项目本身很重要，但更重要是因为认筹的价格足够低。如果，认筹的价格已经消耗掉了所有的市场预期，那么呈现出来的认筹价就必然接近于其市场心理价位，既然如此，其上市总市值为其认筹总资金的3倍，可以视为市场情绪总和的表现。</span></p><p><span>其三，ICO因为各国政策的收紧，形势大不如前，市场存在着严重的恐慌情绪，稍微有点风吹草动，就出现风声鹤唳、惊弓之鸟的状态。市场资金倾</span></p>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  },
  {
    id: Mock.mock('@increment'),
    title: 'EOS深度解读——是超级机遇，还是超级泡沫？',
    author: Mock.mock('@cname'),
    author_avatar: './static/img/avatar_10.png',
    type: 'EOS',
    aspect: '技术',
    remark: '有句玩笑说，“在币圈，只有10%的人买了比特币，也只有10%的人没买EOS”。可见EOS的...',
    content: `<p><span><span>全文大约8000字，建议收听音频版</span><span> </span></span></p><p><span><span>有句玩笑说，</span>“在币圈，只有10%的人买了比特币，也只有10%的人没买EOS”。可见EOS的群众基础有多好。</span></p><p><span><span>小姐姐前段时间的一篇<a href="http://mp.weixin.qq.com/s?__biz=MzU3MTQ0MTI4OQ==&amp;mid=2247483733&amp;idx=1&amp;sn=836eca0b18da0a9e9bbc08fdc907fcbe&amp;scene=21#wechat_redirect" target="_blank">《论底层公链之争》</a>发布后，公众号后台收到几百条留言，其中很多是关于</span>EOS的问题，我当时没能一一回复，今天就从一个投资人判断项目的角度出发，结合着最新项目进展，对EOS进行一次整体的梳理，相信读完之后，你对EOS的理解会更系统更深入。</span></p><section data-tools="135编辑器" data-id="90142"></section><p><strong><span>EOS基本介绍</span></strong></p><p><span>EOS是Block.One公司正在研发的一个区块链底层公链系统，目的是解决现有的区块链应用性能低、安全性差、开发难度高以及过度依赖手续费的问题，实现分布式应用的性能扩展。EOS 提供帐户，身份验证，数据库，异步通信以及在数以万计的CPU或群集上的程序调度。该技术的最终形式是一个区块链体系架构，该区块链每秒可以支持数百万个交易，同时普通用户无需支付使用费用。</span></p><p><span></span></p><p></p><p><span>目前的底层公链离真正的大规模商业应用还有很大的差距，主要表现在区块网络转账速度慢、费用高、系统并发处理能力差，稳定性差、数据存储和安全缺陷等。这是目前底层公链需要解决的问题，这些问题解决不了，商业化应用只是一句空话。EOS如果要成为一个真正能支持大量商业级应用的公链，势必要解决这些问题。</span></p><p></p><p></p><section data-tools="135编辑器" data-id="90142"><section><section></section></section></section><p><strong><span>EOS的优势和创新</span></strong></p><p><strong><span>1、大幅提升底层公链的交易速度</span></strong></p><p><span><span>以太坊以及目前大部分的区块链应用最让人诟病的一点就是</span>“慢”和“拥堵”，特别是在交易量堆积的时候，大家<span>还记得</span>SNT众筹的时候吗，直接就把以太坊的网络堵得瘫痪了。</span></p><p><span>传统金融行业中的VISA和支付宝在实际应用中，都已经能够支持每秒数万笔的交易。而目前比特币实际应用中仅能支持大约每秒7次的转账，以太坊也仅能支持每秒数十次转账。如何在安全的前提下实现高速处理是区块链底层公链突破的重点。</span></p><p><span>采用了DPOS（股份授权证明）共识算法机制的EOS相对传统公链在速度方面优势明显。正处在开发阶段的EOS如果能够顺利实现，很可能成为第一个可以处理商业级运算的去中心化底层公链。<span>采用</span>DPOS算法机制的石墨烯技术，在有限制的测试条件下已经实现了每秒上万次的交易量。后续，EOS将使用并发技术来继续扩展其网络性能，有望实现每秒数百万次的交易处理能力。届时<span>，</span>EOS将解决底层公链的速率和拓展性问题，将可同时支持数千个商业级的分布式应用程序（DAPP）在其平台上运行。</span></p><p><span><span>2、</span><strong><span>提升底层公链的可拓展性，避免硬分叉</span></strong></span></p><p><span>作为一个去中心化的操作系统，当出现分歧时能否达成共识，在避免硬分叉的前提下保持迭代，将成为一个至关重要的问题。</span></p><p><span></span></p>在区块链中，底层的代码相当于现实中的法律。代码中存在bug是无法避免的，如果一个区块链的底层平台出现bug时没有办法修复将是一件十分可怕的事情，会失去用户的信任。之前的BTC和ETH都曾出现过分叉问题，BTC因为速率扩容以及网络拥堵，社区无法达成共识，至今已经出现了多种分叉币。而ETH则主要因为DAO黑客事件，造成社区内激烈争论，最终分叉为ETH和ETC两条链。<p></p><p><span>而EOS从整体的架构设计上解决了这个问题。EOS的约束性合约相当于“宪法”给整个平台下了明确的定义：在公链的运行中，当系统出错的时候，能够根据可读性意图来区分这个错误是否确实是bug，并且来判断社区的修复是否正确。有了这套机制，如果未来有新技术出现，也可以很容易地增加到EOS系统中，有利于系统的升级和迭代。</span></p><p><span>例如，<span>当</span>EOS中出现了类似之前 DAO的黑客事件时，系统中的21个节点将迅速采取行动，冻结黑客账户，然后通过投票采取最有效的处理方式，不会像之前的ETH因无法共识而出现硬分叉的情况。</span></p><p><span>从这方面来看，EOS在稳定性和可拓展性上相对已有的底层公链系统是非常有优势的。</span></p><p><span><span>3、</span><strong><span>用户免费使用网络资源</span></strong></span></p><p><span><span>拿以太坊来说，交易中每次的计算、存储操作、带宽使用都需要</span>GAS费用。而且，这些必需的费用是波动的，能够被使用者设置成非常高的价格，因为矿工总是优先处理给出高手续费的交易<span>，造成的结果是，</span>ETH上随着新项目越来越多，网络使用成本也在不断的提升。</span></p><p><span></span></p><p></p><p><span><span>相比之下，</span>EOS则采用所有权模式，如果你拥有相应比例的代币，就有权免费使用相应比例的网络资源，对你而言，交易将是零成本的。</span></p><p><span><span>4、</span><strong><span><span>安全性高，避免</span>DOS攻击</span></strong></span></p><p><span>DOS攻击在众多网络攻击技术中是一种常见并且具有很大危害性的攻击方式。它通过各种手段消耗系统资源和堵塞网络带宽,使正常网络服务陷于瘫痪状态<span>。在近几年的实践中以太坊网络已经被证明非常容易受到</span>DOS攻击的影响。</span></p><p><span>EOS的代币则相当于网络资源的所有权，用户只能使用相应比例的网络资源。因此，DOS攻击者只能使用到<span>其</span>EOS代币相应比例的带宽资源，形成了天然的制度屏障，使DOS攻击仅可能出现在某一个应用程序上，但这些攻击永远不会破坏整个网络<span>运行，因此</span>EOS从设计上提升了整个基础链的安全性。</span></p><p><span><span>5、</span><strong><span>提升公链系统的兼容性</span></strong></span></p><p><span>为了实现更好的兼容性，EOS中设计了跨链交互和虚拟机独立架构机制。例如，在EOS系统中设置了以太虚拟机（EVM），它能够支持现有的以太坊合约。也就是说，现有的以太坊平台上的应用，只需要稍加配置，未来就能够在EOS上面运行。</span></p><p><span>对于开发者来说，目前在其他公链<span>上开发</span>DAPP并不容易，需要自己写很多基础模块才能实现。而EOS的设计目标是成为区块链的底层操作系统，已经为开发者提供了各类底层开发模块。简单来说，各类基础功能EOS都已经帮你设计好了，你只需要知道怎么使用这些基础工具就可以完成自己的开发了。这样也就大大降低了开发者门槛，<span>再加上</span>EOS平台并发处理速度快，没有手续费的特点，将会吸引更多的普通<span>开发者，有助于</span>EOS平台上涌现大量的商业级应用，快速形成平台生态系统。</span></p><p><span>通过以上分析，我们不难得出结论，从架构设计的角度EOS非常有潜力成为能够支撑大量商业级应用的超级底层公链。</span></p><p><span><span>但</span>EOS还只是一个开发中的项目，在主网没有上线的情况下，列举这些技术解决方案及实现方式，只是出于概念上的论证，至于这种可能性能否最终实现，我们并不确定，作为投资者，想分析清楚一个项目的投资价值，我们还需要从可行性，进展情况等各方面进行多维度的考量。</span></p><section data-tools="135编辑器" data-id="90142"></section><p><strong><span>EOS项目能否顺利落地？</span></strong></p><p><span><strong><span>1、团队层面分析</span></strong></span></p><p><span>说到EOS团队，我们最大期待当然源自技术负责人BM。</span></p><p><span></span></p><p></p><p><span>有一种说法是目前区块链行业能做到连续两个产品都排名前五十市值的，全世界只有三个大神：<span>一个是</span>BM，一个是Ripple和Stellar的创始人Jed&nbsp;McCaleb，还有一个是IOTA和NXT的创始人BNEXT；</span></p><p><span>而<span>能做到连续三个产品都排名前五十市值的，只有一位</span>——就是BM，他的三个项目是EOS、BTS<span>和</span>STEEM。</span></p><p><span>还有另外<span>一种说法是</span>2013年到现在，整个区块链行业中屈指可数有实际应用的四个产品是：比特币，以太坊，STEEM，BTS，而其中的两个<span>都是</span>BM创建的。</span></p><p><span>尽管BM曾经也做过不少<span>社区不同意但自己强行要做的事，正如乔布斯和马斯克一样，优秀的研发者多少有些偏执狂，我对</span> BM的技术能力和格局还是很有信心的，也很憧憬他在白皮书中所描绘的项目远景。</span></p><p><span><strong><span>2、技术实现可能性分析</span></strong></span></p><p><span>EOS的核心架构和底层技术并不是无中生有的，项目<span>核心的共识算法是</span>DPOS（委托权益证明），是基于石墨烯技术实现的，<span>已经在</span>BM的前两个项目BTS和STEEM中得以应用和验证，这两个项目已经落地运行多年，每天都有数万级用户在使用，堪称除了BTC和ETH之外，应用最成功的区块链项目。</span></p><p><span><span>并且石墨烯技术在有限制条件的实测下已经达到了</span>3300TPS的速率，EOS希望在此基础上大幅提高性能<span>至百万级</span>TPS，挑战不小，但这并不是一个质变的过程，而是实践基础的量变过程，实现<span>难度要比从</span>0到1小很多。</span></p><p><span>此外，EOS接近一年的众筹，还在进行之中，目前已经筹集了超过4亿美元的资金，这些资金足以保证为项目配置顶级的开发团队，所以因资金致使项目终止的可能性也不大。</span></p><p><strong><span>3、项目的进展情况分析</span></strong></p><p><span>我们先整体看看 EOS.IO 软件开发路线图：</span></p><section data-tools="135编辑器" data-id="24"><section><section><p><span><strong><span>阶段 1</span></strong><span>（构建最小可行的测试环境，2017 年夏季完成）：创建 API，让开发人员可以在 EOS.IO 上开始构建和测试应用。</span></span></p><p><span><strong><span>阶段 2</span></strong><span>（构建最小可行的测试网络，2017 年秋季完成）：完成 P2P 网络代码，WASM 清理与 CPU 沙盒化，开发工具使到 EOS Token 的发布状态可导入数据，并创建一个创世设置文件，让参与 Token 公募发布的人们获得一些初始的测试 EOS（TEOS）。</span></span></p><p><span><strong><span>阶段 3</span></strong><span>（进行测试与安全审计，2017年冬季-2018年春季完成）：对平台进行大量测试，找出安全问题和程序 bug，完成程序开发示例，提供说明文档与教程，形成 EOS.IO 软件 1.0 版本。</span></span></p><p><span><strong><span>阶段 4</span></strong><span>（并行优化， 2018 年夏季-秋季完成）：继续优化代码，提升并行执行性能。</span></span></p><p><span><strong><span>阶段 5</span></strong><span>（实现集群， 未来）</span></span></p></section></section></section><p><span>目前的开发处于第三阶段。</span></p><p><span>EOS.IO 软件的 Dawn 3.0 已经于 2018 年1月发布了早期版本，开发者已经可以根据该版本来搭建开发测试环境。</span></p><p><span>小姐姐写到这里时，顺便打开了 EOS.IO 的 github（https://github.com/EOSIO/eos），看了看目前的代码开发情况：</span></p><p></p><p></p><p><span>从网站上可以看到，EOS.IO 的 github 代码库目前共有 76 名开发人员参与，代码库已经被更新了 3494 次， 同时已经被 fork 了 1058 次（相当于被克隆了1058 次，一般开发者觉得代码有用才会 fork，该数据能够体现开发者对该项目的关注度）。</span></p><p></p><p></p><p><span><span>2月8日，BM<span>在接受采访时表示，</span>EOS从Steem和BitShares那里学到了所有的教训，将把本地代码的速度提升80%。而且很多主流交易所都已经和EOS团队联系，EOS将会出现在更多更好的交易所，也会有更好的社交网络和</span><span>预期市场</span><span>。<span>从种种情况来看，</span>EOS目前的进展还算顺畅。</span></span></p><p><span>同时有消息说称<span>全球最大的比特币交易平台</span>Bitfinex准备在EOS上建立去中心化交易所。</span></p><p><span>另外一个值得注意的<span>消息是：美国知名的独立评级机构</span>Weiss Ratings在1月25日发布的评级报告中，EOS和ETH获得“B”评级，这也是所有数字货币中最高的评级，从一个侧面证明EOS项目的进展情况和发展潜力得到了认可。</span></p><p></p><p></p><p><strong><span>投资价值评估</span></strong></p><p><span><strong><span>1、底层公链的深层价值</span></strong></span></p><p><span>对于底层公链的价值和竞争分析，小姐姐在之前的一篇文章《论底层公链之争》中已经做过阐述。</span></p><p><span></span></p><p></p><p><span>简而言之，更快更强更安全的底层基础链是今天区块链世界的刚需。我们需要一个基于智能合约的速度更快，扩展性更强，生态更稳定的底层设施，如果有一条这样的基础链诞生，承载起这样的历史使命，它完全有资格改变整个区块链世界的格局。</span></p><p><span>所以，3.0级别的基础公链，其估值远非普通应用级项目所能比拟的。作为投资者的我们，有必要对这样的项目多些关注。</span></p><p><strong><span>2、EOS整体生态体系的价值</span></strong></p><p><span>EOS是一个区块链基础架构或者说是生态架构，如果能够顺利实现将是未来区块链的基础设施，对于投资者而言是个有足够大想象空间的投资标的。</span></p><p><span>所谓的生态系统，应该包括良好的底层架构（性能，速率，安全性，兼容性等）、合理的共识机制、丰富的各类应用、大量用户组合而成，并且相互之间协调运作，顺畅配合。</span></p><p><span>如果把EOS的整个生态系统比作一座城市，底层公链EOS就相当于这座城市的基础设施（水，电，土地，公路等等），各类DAPP就相当于上面大大小小各类建筑物，当基础设施足够好，就会出现越来越多的上层建筑物，如果再配合良好的城市管理制度（共识机制），则一定会吸引大量居民（用户）涌入，并最终形成一个繁荣的城市生态系统。</span></p><p><span></span></p><p></p><p><span>从投资角度而言，一个底层公链项目除了考量其技术和性能等基本条件外，未来能否搭建起一整套繁荣兴盛的生态系统，是我们考量其长期价值的核心。</span></p><p><strong><span>3、EOS代币的价值</span></strong></p><p><span>EOS代币相当于EOS系统的资源所有权。如果你持有1%的代币，相当于你拥有1%的计算资源。无论其他的DAPP怎么堵塞，你的1%带宽并不受到影响。</span></p><p><span>也就是说EOS代币代表着自己权利的多少，谁拥有更多的EOS，谁拥有更多的权益。</span></p><p><span>在以太坊上面，系统使用者越多，堵塞程度越严重，交易费用越高。而在EOS上面运行DAPP，项目方必须手头拥有一定数量的EOS作为网络资源的保证。DAPP的使用者越多，需要的带宽资源就越大，相对的项目方需要的EOS必须越多，所以项目方就会更有意愿购买EOS。</span></p><p><span><span>试想一下，如果</span>EOS顺利落地，诞生了一批拥有大量用户的超级应用，到那时，你手里的EOS会价值几何？&nbsp;</span></p><p><span><span>一旦成功打造出</span>EOS公有链，众多新项目必定会需要EOS，而这种模式也正是曾经以太坊价格暴涨的真正原因。</span></p><p><span>2017年初以太坊的价格<span>还只有不到</span>100人民币，而一年之后曾暴涨到<span>高点时的</span>10000元以上。</span></p><p><span>其中原因，主要是<span>因为</span>i西欧对ETH形成了几十亿美元的巨大需求。实际上，ETH成了货币媒介，i西欧扩大了以太坊的市场需求使其价格节节攀升。</span></p><p><span><span>在整个</span>2017年，在以太上i西欧的区块链项目有上千个之多<span>，光是</span>EOS发行的第一阶段，就募集了<span>高达</span>1.8亿美元的以太坊。</span></p><p><span>可以说没有强大的应用和众多项目的支撑，就不会有以太坊如今的价格。</span></p><p><span>同样的道理，<span>基于</span>EOS公有链去开发项目的团队需要持有或租用EOS，才能分配到网络资源。所以，<span>未来基于</span>EOS架构开发新项目的团队必然<span>会选择募集</span>EOS，<span>因此，</span>EOS既成为媒介工具，又成为开发团队的必备。作为公有链的EOS吸引的团队越多，其价格会攀升得越高。</span></p><p><span></span></p><p></p><p><strong><span>EOS项目风险评估</span></strong></p><p><span><strong><span>1、项目失败的风险</span></strong></span></p><p><span>对于所有还没真正落地的区块链项目而言，项目失败的风险都是很大的，即使作为明星项目的EOS，毕竟还处在开发阶段，开发计划中的核心指标还未实现。在这种情况下，作为投资者的我们自然不能忽视项目存在中断或者失败的可能性。但综合各方面信息来分析，这种可能性不大，小姐姐认为，EOS的项目失败风险小于90%以上的区块链项目。EOS被某些人称为空气项目，如果按照这样的说法，那么市面上99%的项目也都是空气。</span></p><p><span>EOS 项目早期的开发及募资都将由Block.one完成，而且在白皮书中，公司并没有承诺后续的义务和责任，另外该公司的注册地是在开曼群岛，<span>这也算是</span>EOS常常被大家诟病的话题了。其实从一个投资人的角度来看，我觉得这些也并不奇怪。很多基金公司都会出于避税和规避潜在的法律风险而把公司注册在开曼群岛等地，另外，白皮书没有太多承诺责任和义务，在区块链行业中也是很普遍的情况。根据这些显然还不足以判断EOS是个不靠谱的项目。</span></p><p><strong><span>2、技术实现未达预期的风险</span></strong></p><p><span>EOS项目在某些方面的指标未能达到设计预期，小姐姐认为这应该是投资EOS最大的风险所在。</span></p><p><span>EOS此刻所做的事，可以说将把整个底层公链的水准推升到一个前所未有的高度，这件事本身一定是充满挑战的。虽然说 EOS 底层算法源于 BitShares 和 Steemit两个已经成功落地的项目，但是这两个项目都仅仅是 DApp，而EOS则是更基础的底层平台，在开发难度上还是有很大差别的。因此，对于EOS某些设计指标无法实现的可能性，作为投资者的我们应该进行充分的考量并作出自己的判断。</span></p><p><span><span>投资者投的都是对项目的预期。</span>EOS主链6月份上线后，如果未能达到大家的心理预期，价格必然会出现下调<span>。此外由于</span>EOS本身有通胀机制,每年最高可以有5%的新币增发来奖励'数据包'验证者（可以理解为比特币的矿工），需要有持续的新资金流入，才能消化这部分通胀出来的新币。</span></p><p><span></span></p><p></p><p><strong><span>3、未来公链领域的竞争风险</span></strong></p><p><span>底层公链<span>是目前整个区块链行业的必争之地，竞争会愈发激烈。区块链技术的发展速度很快，未来一定会有更多的共识机制或算法出现，也会不断地有挑战者加入到公链的竞争中，如果出现颠覆性新技术或新思路，对于</span>EOS而言，也将是一个较大的挑战。</span></p><p><span></span></p><p></p><p><span>目前来看，如果能够顺利实现，EOS将是颇具革命性的高性能公链架构，被现有技术击败的可能性不大。但谁也无法预测，未来更新、更强、更快、更安全的技术会在哪天出现。</span></p><p><strong><span>4、EOS生态恶化风险</span></strong></p><p><span>EOS代币代表了系统资源和带宽的使用权利，持有越多，就可以调用更多的资源；</span></p><p><span><span>一些</span>EOS的信仰者并不愿意卖掉手上的代币，这也间接提高了初创项目的<span>成本；如果</span>EOS生态系统成为一个庞然大物，<span>代币可能将变得十分昂贵。此时，随着这些项目的用户增加，高昂的成本可能会反过来限制这些应用的发展，有可能迫使新的应用流失至其他生态平台上，失去了新鲜血液的注入，有可能影响</span>EOS生态系统的发展根基。</span></p><p><span>尽管EOS长达近一年的众筹，希望尽量让代币分散化，但小姐姐认为，上述生态环境恶化的问题未来还是很可能出现的。</span></p><p><strong><span>5、EOS共识算法能否被广泛认同</span></strong></p><p><span>一个公链的共识算法，相当于他的价值观，EOS的共识算法能否与区块链行业的发展趋势相吻合，也很大程度上决定了未来其整个生态体系的构建速度。</span></p><p><span>BM<span>曾经与比特币创始人中本聪辩论，也曾和以太坊创始人</span>“V神”互怼，核心争论内容就是其原创的DPOS共识算法的安全问题，当然，还有是否属于去中心化的问题。</span></p><p><span>EOS的DPOS共识机制能够有效的提升效率，但也被一些人质疑为更接近中心化的架构。BM认为，相对于算力资源仅被少数几个矿池所掌控的BTC和ETH而言，EOS由投票选出的21个节点共同决策的机制，实际上更不容易出现垄断和中心化，是最去中心化的算法。可见，对于算法机制的不同理解和争论还会长期存在，作为投资者的我们也应该做出自己对于未来趋势的判断。</span></p><p><strong><span>6、团队风险</span></strong></p><p><span>BM的个人影响力在整个项目中占据非常核心的地位。</span></p><p><span><span>从历史来看，</span>BM参与创立的STEEM和BTS，都半道出走了，这一次EOS会<span>不会也出现类似的状况呢？前几天出现了</span>BM出走的消息，他本人亲自出来进行了辟谣，暂时打消了大家的顾虑。</span></p><p><span>每个项目都有风险，我们都应该客观理性地去分析看待。</span></p><p><span><span>从小姐姐自身的判断而言，</span><strong>EOS的整体风险等级并不算太高，相对于其他数字货币项目来说，EOS最多属于中度风险的项目，共识基础还是很好的。&nbsp;</strong></span></p><p><strong></strong></p><p></p><p><span>最近EOS的整体表现相对较弱，对它的判断也出现了各种各样的声音。有人依然坚定的视它为信仰，也有人说它是圈钱的空气项目。</span></p><p><span>以上是小姐姐从投资评估的角度为大家做的梳理，看过之后你是否有了自己的判断呢？</span></p><p><strong><span>一个理性的投资行为，需要考虑很多的因素，包括自身的投资风格、行业环境的最新变化、项目所处的阶段、竞争形势、整个市场的行情等等。</span></strong></p><p><span>拿来主义对我们自己没有任何好处，思考能力不是天生的，是在后天锻炼出来的。如果自己能试着站在行业发展进程的角度，甚至是从历史发展的角度去审视，你看到的将是不一样的世界。</span></p><p><strong><span>对于EOS，小姐姐非常肯定它在现阶段的潜力和价值，也认为它取得成功的可能性还是很大的。</span></strong></p><p><span>但它毕竟还只是一个婴儿，<strong>尽管基因优秀，但如同整个区块链行业一样，充满了不确定性。</strong></span></p><p><span>如果作为短期投资，EOS并不适合，它需要时间去生长，投资者至少做好持有2-3年的准备，前提是这个过程中团队没有出现大的失误或者行业发生大的迭代。</span></p><p><strong><span>建议将EOS作为对底层公链的布局来进行适当配置</span></strong><span>，或许数年之后，它会给你一个大大的惊喜。</span></p><p><span><span><span></span><span></span></span></span></p><blockquote><p><span>针对今天的话题，我们单独创建了一个</span><span>“EOS交流”微信群。</span><span>欢迎大家进群深度探讨，要求是EOS的持有者，或对EOS有较深入的了解。</span></p></blockquote>`,
    coin_logo: './static/img/eos_logo.png',
    coin_name: 'EOS超级社区',
    coin_member: '2046',
    coin_people_list: ['./static/img/avatar_7.png', './static/img/avatar_8.png', './static/img/avatar_9.png', './static/img/avatar_10.png', './static/img/avatar_1.png', './static/img/avatar_2.png']
  }
]
export default {
  getList: config => {
    const { aspect, type, id, page, limit } = param2Obj(config.url);
    let mockList = List.filter(item => {
      if (aspect && item.aspect !== aspect) return false;
      if (type && item.type !== type) return false;
      if (id && item.id !== Number(id)) return false;
      return true;
    });
    const pageList = mockList.filter((item, index) => index < limit * page && index >= limit * (page - 1));
    return {
      total: mockList.length,
      items: pageList
    }
  }
};



