// import { saveToLocal, loadFromLocal} from 'utils/localstorage';
const user = {
    state: {
        token: '',
        join: false
    },

    mutations: {
        SET_TOKEN: (state, token) => {
            state.token = token;
        },
        SET_JOIN: (state, join) => {
            state.join = join;
        },
    },
    actions: {
        // 新增用户信息方法
        UpdateJoin({ commit, state }, data) {
            let join = window.localStorage.setItem('join_status', data.join)
            commit('SET_JOIN', join)
        },
        
        //初始化获取用户信息方法
        InitJoin({ commit, state }, data) {
            if (window.localStorage.getItem('join_status')) {
                state.join = JSON.parse(window.localStorage.getItem('join_status'))
            }
            else state.join = false
        },
    }
};
export default user;



