
import { mapState } from 'vuex'
import Loading from 'vux/src/components/loading/index.vue'

import { AppHeader, AppFooter, AppMain } from 'views/layout';
export default {
name: 'Layout',
data () {
return {
title: '加载中'
}
},
computed: {
...mapState({
isLoading: state => state.vux.isLoading
})
},
components: {
AppHeader,
AppFooter,
Loading,
AppMain
},
methods: {
}
}



