import Vue from 'vue'
import Router from 'vue-router'
/* Layout */
import Layout from '../views/layout/Layout';
/* index */
const index = resolve => require(['../views/index'], resolve);
const detail = resolve => require(['../views/detail'], resolve);
/* error page */
const Err404 = resolve => require(['../views/error/404'], resolve);

Vue.use(Router)

export const constantRouterMap = [

  {
    path: '/',
    redirect: '/',
    component: Layout,
    name: 'index',
    hidden: true,
    children: [{ path: '/', meta: { title: '个人简历' }, component: index }]
  },
  {
    path: '/detail/:id',
    redirect: '/detail/:id',
    component: Layout,
    name: 'detail',
    hidden: true,
    children: [{ path: '/detail/:id', meta: { title: '详情' }, component: detail }]
  },
  {
    path: '/404',
    redirect: '/404',
    component: Layout,
    name: 'Err404',
    hidden: true,
    children: [
      { path: '/404', component: Err404, meta: { title: '404' }, name: '404' },
    ]
  },
  { path: '*', redirect: '/404', hidden: true }
]
export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});



